@-webkit-keyframes blink {
  0% {
    opacity: 0.6;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0.6;
  }
}

@keyframes blink {
  0% {
    opacity: 0.6;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0.6;
  }
}
.scroll-hint {
  display: none;
  height: 2vh;
  margin: 0 auto;
  animation: blink 1s infinite;
  -webkit-animation: blink 1s infinite;
}

@media (max-width: 1000px) {
  .scroll-hint {
    display: block;
  }
}
