.navArrow {
  vertical-align: middle;
  display: inline-block;
  height: 100%;
  width: auto;
}

.navArrow-left {
  transform: rotate(90deg) translateY(50%);
  -webkit-transform: rotate(90deg) translateY(50%);
  -moz-transform: rotate(90deg) translateY(50%);
  -ms-transform: rotate(90deg) translateY(50%);
  -o-transform: rotate(90deg) translateY(50%);
}

.navArrow-right {
  transform: rotate(-90deg) translateY(50%);
  -webkit-transform: rotate(-90deg) translateY(50%);
  -moz-transform: rotate(-90deg) translateY(50%);
  -ms-transform: rotate(-90deg) translateY(50%);
  -o-transform: rotate(-90deg) translateY(50%);
}

.navArrow-top {
  transform: rotate(180deg);
  -webkit-transform: rotate(180deg);
  -moz-transform: rotate(180deg);
  -ms-transform: rotate(180deg);
  -o-transform: rotate(180deg);
}

.lighten {
  height: 2.5vh;
  font-size: 2vh;
  display: inline-block;
  padding: 0.7em;
  color: #FFF;
  opacity: 0.4;
}
.lighten:hover {
  opacity: 1;
}
.lighten:hover::after, .lighten:hover::before {
  display: inline-block;
  vertical-align: middle;
}

.lighten-left:hover {
  border-right: 2px solid #e2c68a;
}
.lighten-left:hover::after {
  position: relative;
  content: attr(b);
}

.lighten-right:hover {
  border-left: 2px solid #e2c68a;
}
.lighten-right:hover::before {
  position: relative;
  content: attr(b);
}

.lighten-bottom {
  height: auto;
}
.lighten-bottom img {
  height: 2.5vh;
}
.lighten-bottom:hover::before {
  border-top: 2px solid #e2c68a;
  display: block;
  padding: 0.5em;
  padding-bottom: 0.7em;
  content: attr(b);
}

.lighten-top:hover::after {
  position: relative;
  display: block;
  padding: 0.5em;
  padding-bottom: 0.7em;
  content: attr(b);
  border-bottom: 2px solid #e2c68a;
}

